import React, { Component } from 'react';
import { HashRouter as Router, Route, Redirect, Switch } from 'react-router-dom';
import { connect } from 'react-redux';
import './billing.css';

import GoogleLogin from '../../auth/google';
import Billing from "../../tools/billing";
import Downloads from '../../containers/downloads';

class Routes extends Component{

    constructor(props){
        super(props);
        this.state = {
            configParamsUpdated: false
        }

    }

    componentWillMount(){
        console.log("Billing Routes container");
    }

    componentDidMount(){
    }

    render(){
        return (
            this.props.userAuthData.status ?
            <div className="main_component">
                <Router>
                    <Switch>
                        {
                            this.props.config.downloads &&
                            <Route path={"/downloads"} render={(props) => {  return <Downloads {...props} />}}/>
                        }
                        <Route path={"/"} render={(props) => {  return <Billing {...props} />}}/>
                        <Redirect to='/' />
                    </Switch>
                </Router>
            </div>
            
            :
            <div>
                <GoogleLogin />
            </div>
        )
    }
}

const mapStateToProps = state => ({
    config: state.config,
    userAuthData: state.userAuthData
});
  
const mapDispatchToProps = dispatch =>({
    
});

export default connect(mapStateToProps, mapDispatchToProps)(Routes);